.main-wrapper {
    position: relative;
    width: 100%;
    height: 100vh;
    /* 화면 전체 높이 */
    /* overflow: hidden; */
}

.background-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
    filter: brightness(65%);
}

.container {
    padding: 20px;
    position: relative;
    z-index: 1;
    align-items: center;
}

/* 상단 네비게이션 */
.top-nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 40px;
}

.profile {
    position: absolute;
    display: flex;
    gap: 17px;
    top: 35px;
    right: 35px;
    font-size: 30px;
    color: #ffffff;
    z-index: 1000;
}
.material-symbols-outlined{
    font-size: 40px;
}
.profile :hover {
    cursor: pointer;
}

.shop:hover {
    cursor: pointer;
}

.name:hover {
    cursor: pointer;
}

/* 카드 버튼들 */
.card-container {
    padding-top: 190px;
    display: flex;
    justify-content: center;
}

.health-card {
    text-decoration: none;
    background-color: #f28500;
    padding: 20px 10px;
    margin: 0 35px;
    width: 280px;
    height: 450px;
    box-shadow: 0px 10px 15px rgba(255, 255, 255, 0.1);
    cursor: pointer;
    transition: transform 0.2s;
    border-radius: 80px;
    color: white;
    font-family: 'Gowun Batang', serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* 세로 중앙 정렬 */
    align-items: center;
    /* 가로 중앙 정렬 */
    margin: 0 2.5rem;
}

.health-card:hover {
    transform: translateY(-20px);
}

.card-text {
    font-size: 2rem;
    margin-bottom: 20px;
    text-align: center;
    text-decoration: none;
}

.card-text h2 {
    font-size: 2rem;
    margin-bottom: 15px;
}

.card-text p {
    font-size: 1.2rem;
    line-height: 1.5;
}