.editProfileContainer {
    width: 400px;
    height: 750px;
    border: 3px solid #f28500;
    border-radius: 30px;
    margin-top: 1.75rem;
    margin-left: 3rem;
}

.editProfileContainer>h3 {
    margin-left: 2rem;
    margin-top: 1rem;
}

.editProfileContainer>hr {
    width: 350px;
    margin: 0 auto;
    margin-top: 1rem;
    background-color: lightgray;
}

.editProfileContainer>.topHr {
    border: 2px solid black;
}

.profileInfo {
    margin-left: 2rem;
    margin-top: 1rem;
    display: flex;
    justify-content: space-between;
}

.profileInfo>input {
    margin-right: 3rem;
    height: 2rem;
    border-radius: 5px;
    padding: 5px;
}

.editProfileContainer>button {
    width: 200px;
    height: 50px;
    margin-left: 100px;
    margin-top: 20px;
    border-radius: 10px;
    border-style: none;
    background-color: orange;
    cursor: pointer;
    font-size: 1rem;
}

.editProfileContainer>button:hover {
    background-color: #ff9900;
    /* color: orange; */
}