.wrtModal {
    position: fixed;
    top: 136px;
    width: 600px;
    height: 600px;
    background-color: #F1E5D4;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    border-radius: 30px 10px 10px 10px;
    padding-top: 20px;
    margin-top: 10px;
}

.msgDestination {
    display: flex;
    width: 80%;
    justify-content: space-between;
    margin-top: 20px;
}

.msgDestination input[type='text'] {
    width: 80%;
    height: 2rem;
    padding-left: 10px;
    font-size: 1rem;
}

.msgDestination>h3 {
    margin-right: 10px;
    margin-top: 6px;
}

.msgContent {
    display: flex;
    width: 80%;
    margin-top: 20px;
}

@font-face {
    font-family: 'SeoulNamsanM';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/SeoulNamsanM.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.msgText {
    width: 80%;
    height: 400px;
    padding: 5px 10px;
    margin-left: 62px;
    font-size: 1rem;
    font-family: 'SeoulNamsanM';
    font-size: 1.5rem;
}

.modalButtons {
    margin-top: 45px;
    width: 90%;
    display: flex;
    justify-content: space-between;
}

.modalButtons>.quitBtn {
    padding: 10px 20px;
    border-radius: 10px;
    border: 1px solid lightgray;
    cursor: pointer;
}

.modalButtons>.sendBtn {
    padding: 10px 20px;
    border-radius: 10px;
    border: 1px solid lightgray;
    cursor: pointer;
}

.modalButtons>.quitBtn:hover {
    background-color: orange;
    color: white;
}

.modalButtons>.sendBtn:hover {
    background-color: orange;
    color: white;
}